:root {
    /* 자연물 (산, 흙 등...) 어울리는 색입니다. */
    --soft-color-blue: #8ECEF2;
    --soft-color-green: #66CD79;
    --soft-color-brown: #9E8A6C;
    --soft-color-ivory: #DFBC94;   

    /* 도표, 폰트 등의 색입니다. */
    --hard-color-black: #3E3A3A;
    --hard-color-grey: #948B8B;
    --hard-color-white: #F6F6F6;
    --hard-color-green: #12D20E;
    --hard-color-green-selected: #129f0f;

    /* 지역 색입니다. */
    --region-color-one: #FF5C5C;
    --region-color-two: #ffd438;
    --region-color-three: #a628e0;
    --region-color-four: #0084ff;
}

button {
    transition: background-color 0.3s ease-in-out;
}